import { Box, Button, MenuButton, Menu, Text, Tooltip, Avatar, MenuList, MenuItem, MenuDivider, Drawer, 
    DrawerOverlay, DrawerContent, DrawerHeader, DrawerBody, useToast, Spinner, effect } from '@chakra-ui/react';
    import { useDisclosure } from '@chakra-ui/hooks';
import {BellIcon,ChevronDownIcon} from '@chakra-ui/icons'
import React, { useState } from 'react'
import { ChatState } from '../../context/ChatProvider';
import ProfileModal from './ProfileModal';
import { useHistory } from 'react-router-dom';
import axios from 'axios';
import ChatLoading from '../ChatLoading';
import UserListItem from '../UserAvatar/UserListItem';
import NotificationBadge from 'react-notification-badge';
import {Effect} from 'react-notification-badge';
import { getSender } from '../config/ChatLogics';

const SideDrawer = () => {
    const {isOpen,onOpen,onClose}=useDisclosure();
    const [search, setSearch] = useState("");
  const [searchResult, setSearchResult] = useState([]);
  const [loading, setLoading] = useState(false);
  const [loadingChat, setLoadingChat] = useState();
  
  const {user,setSelectedChat,chats,setChats,notification,setNotification}=ChatState();
  
  const history=useHistory();
  const toast=useToast();
  const logoutHandler=()=>{
      localStorage.removeItem("userInfo");
      history.push('/');
  } 
  const handleSearch=async()=>{
      if(!search)
      {
        toast({
            title: "Please Enter something in Search",
            status: "warning",
            duration: 5000,
            isClosable: true,
            position: "bottom",
          });
          return;
      }
      try {
          setLoading(true)
          const config={
              headers:{
                  Authorization:`Bearer ${user.token}`
              }
          } 
          const {data}=await axios.get(`/api/user?search=${search}`,config)
          setLoading(false);
          setSearchResult(data);
      } catch (error) {
        toast({
            title: "Failed to load the search Results",
            status: "error",
            duration: 5000,
            isClosable: true,
            position: "bottom-left",
          });
      }
  }
  const accessChat=async(userId)=>{
    try {
        setLoadingChat(true)
        const config={
            headers:{
                "Content-Type":"application/json",
                Authorization:`Bearer ${user.token}`
            }
        }
       const {data}=await axios.post('/api/chat',{userId},config);
       setSelectedChat(data);
       setLoadingChat(false);
       onClose(); 
    } catch (error) {
        toast({
            title: "Error fetching the chat",
            description:error.message,
            status:'error',
            duration: 5000,
            isClosable: true,
            position: "bottom-left",
          });
    }
  }
  return (
        <>
        <Box
        d="flex"
        justifyContent='space-between'
        alignItems="center"
        bg="white"
        w="100%"
        p="5px 10px 5px 10px"
        borderWidth="5px"
        >
            <Tooltip label="Search Users to chat" hasArrow placement='bottom-end'>
            <Button variant="ghost" onClick={onOpen}>
                <i className='fas fa-search'></i>
                <Text
                d={{base:'none',md:"flex"}}
                px='4'
                >
                 Search User
                </Text>
            </Button>
            </Tooltip>
         <Text fontSize='2xl' fontFamily="Work sans">
             TalkerZone
         </Text>
         <div>
             <Menu>
                 <MenuButton p={1}>
                 <NotificationBadge count={notification.length} effect={Effect.SCALE}/>
                     <BellIcon fontSize='2xl' m={1}/>
                 </MenuButton>
             <MenuList pl={2}>
                 {!notification.length && "no new messages"}
                 {notification.map(notif=>(
                     <MenuItem key={notif._id} onClick={()=>{
                         setSelectedChat(notif.chat)
                         setNotification(notification.filter((n)=>n!==notif));
                         console.log(notif);
                     }}>
                         {notif.chat.isGroupChat ? `New Message in ${notif.chat.chatName}`
                          : `New Message from ${getSender(user,notif.chat.users)}`}
                     </MenuItem>
                 ))}
             </MenuList>
             </Menu>
            <Menu>
            <MenuButton as={Button} rightIcon={<ChevronDownIcon/>}>
                     <Avatar size="sm" cursor='pointer' name={user.name}
                         src={user.pic}
                     />
                 </MenuButton>
                 <MenuList>
                 <ProfileModal user={user}>
                 </ProfileModal>
                     
                     <MenuDivider/>
                     <MenuItem onClick={logoutHandler}>Logout</MenuItem>
                 </MenuList>
            </Menu>
         </div>
        </Box>    
        <Drawer placement='left' onClose={onClose} isOpen={isOpen}>
            <DrawerOverlay/>
            <DrawerContent>
            <DrawerHeader borderBottomWidth="1px" >Search Users</DrawerHeader>
            <DrawerBody>
                <Box d="flex" pb={2}>
                <input placeholder='Search by name or email'
                    mr={2}
                    value={search}
                    onChange={(e)=>setSearch(e.target.value)}
                />
                <Button 
                 onClick={handleSearch}
                >
                Go</Button>
                </Box>
                {loading?(<ChatLoading/>):(searchResult?.map(user=>
                
                <UserListItem
                    key={user._id}
                    user={user}
                    handleFunction={()=>accessChat(user._id)}
                />
                ))}
            </DrawerBody>
            {loading && <Spinner ml="auto" d="flex"/>}
            </DrawerContent>
               
        </Drawer>
        </>
    )
}

export default SideDrawer
