import { FormControl, FormLabel, Input, VStack,Button,InputRightElement,InputGroup,useToast } from '@chakra-ui/react'
import React, { useState } from 'react'
import axios from 'axios';
import {useHistory} from 'react-router-dom';
const Signup = () => {
    const [show, setShow] = useState(false);
    const handleClick = () => setShow(!show);
    const [name, setName] = useState();
    const [email, setEmail] = useState();
    const [confirmpassword, setConfirmpassword] = useState();
    const [password, setPassword] = useState();
    const [pic, setPic] = useState();
    const [loading,setLoading]=useState(false);
     const history=useHistory();
    const toast=useToast();
       const submitHandler = async () => {
        setLoading(true);
        if (!email || !password) {
          toast({
            title: "Please Fill all the Feilds",
            status: "warning",
            duration: 5000,
            isClosable: true,
            position: "bottom",
          });
          setLoading(false);
          return;
        }
        try {
          const config={
            headers:{
              "Content-type":"application/json"
            }         
          }
          const {data}=await axios.post(
            '/api/user',
            {name,email,password,pic},
            config
          );
          toast({
            title:"Registration Succesful",
            status:"success",
            duration:5000,
            isClosable:true,
            position:"bottom"
          })
          localStorage.setItem("userInfo",JSON.stringify(data));
          setLoading(false);
          history.push('/chats');      
        } catch (error) {
          toast({
            title:"Error Occured",
            description:error.response.data.message,
            status:"warning",
            duration:5000,
            isClosable:true,
            position:"bottom"
          })
        }
      }
    
       const postDetails=(pics)=>{
      setLoading(true);
      if(pics===undefined){
       toast({
         title:"Please Select an Image",
         status:"warning",
         duration:5000,
         isClosable:true,
         position:"bottom"
       })
       return;
    }
    if(pics.type==="image/jpeg" || pics.type==="image/png")
    {
      const data=new FormData();
      data.append("file",pics);
      data.append("upload_preset","talkzone");
      data.append('cloud_name','bestimg123');
      fetch('https://api.cloudinary.com/v1_1/bestimg123/image/upload',
      {method:"post",
       body:data,
      }).then((res)=>res.json())
      .then(data=>{
        setPic(data.url.toString());
        console.log(data.url.toString());
        setLoading(false);
      })
      .catch((error)=>{
        console.log(error);
        setLoading(false);
      })
    }
    else{
      toast({
        title:"Please Select an Image",
        status:"warning",
        duration:5000,
        isClosable:true,
        position:"bottom"
      })
      setLoading(false);
      return;
    }
    
    }
    return (
        <VStack spacing="5px">
        <FormControl id="first-name" isRequired>
          <FormLabel>Name</FormLabel>
          <Input
            placeholder="Enter Your Name"
            onChange={(e) => setName(e.target.value)}
          />
        </FormControl>
        <FormControl id="email" isRequired>
          <FormLabel>Email Address</FormLabel>
          <Input
            type="email"
            placeholder="Enter Your Email Address"
            onChange={(e) => setEmail(e.target.value)}
          />
        </FormControl>
        <FormControl id="password" isRequired>
          <FormLabel>Password</FormLabel>
          <InputGroup size="md">
            <Input
              type={show ? "text" : "password"}
              placeholder="Enter Password"
              onChange={(e) => setPassword(e.target.value)}
            />
            <InputRightElement width="4.5rem">
              <Button h="1.75rem" size="sm" onClick={handleClick}>
                {show ? "Hide" : "Show"}
              </Button>
            </InputRightElement>
          </InputGroup>
        </FormControl>
        <FormControl id="password" isRequired>
          <FormLabel>Confirm Password</FormLabel>
          <InputGroup size="md">
            <Input
              type={show ? "text" : "password"}
              placeholder="Confirm password"
              onChange={(e) => setConfirmpassword(e.target.value)}
            />
            <InputRightElement width="4.5rem">
              <Button h="1.75rem" size="sm" onClick={handleClick}>
                {show ? "Hide" : "Show"}
              </Button>
            </InputRightElement>
          </InputGroup>
        </FormControl>
        <FormControl id="pic">
          <FormLabel>Upload your Picture</FormLabel>
          <Input
            type="file"
            p={1.5}
            accept="image/*"
            onChange={(e) => postDetails(e.target.files[0])}
          />
        </FormControl>
        <Button
          colorScheme="blue"
          width="100%"
          style={{ marginTop: 15 }}
          onClick={submitHandler}
          isLoading={loading}
        >
          Sign Up
        </Button>
      </VStack>    )
}

export default Signup
